import { Copyright } from "./Copyright/Copyright";
import * as S from "./Styles";
import ContactBtn from "./ContactBtn/ContactBtn";
import {
  FaVk,
  FaYoutube,
  FaTwitter,
  FaInstagram,
  FaOdnoklassniki
} from "react-icons/all";
import data from "../../LocalData/socialnet-footer-links.json";

export const Footer = () => {
  return (
    <>
      <ContactBtn />
      <S.Footer id="contacts">
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <S.Heading> Finevideo</S.Heading>
              <S.Paragraph>
                        
                   .   
                    , 
                    .
              </S.Paragraph>
              <S.Paragraph className="mb-lg-4 mb-0">
                     ,    
                .       .
              </S.Paragraph>
            </div>

            <div className="col-lg-3">
              <S.Heading>  </S.Heading>
              <S.LinksContainer className="fa-ul mb-lg-4 mb-0 p-0">
                {data.map(({ name, svg, href }) => (
                  <li key={svg}>
                    <S.Link href={href}>
                      {svg === "vk" && <FaVk />}
                      {svg === "youtube" && <FaYoutube />}
                      {svg === "twitter" && <FaTwitter />}
                      {svg === "odnoklassniki" && <FaOdnoklassniki />}
                      {svg === "instagram" && <FaInstagram />}
                      {name}
                    </S.Link>
                  </li>
                ))}
              </S.LinksContainer>
            </div>

            <div className="col-lg-3">
              <S.Heading> </S.Heading>
              <address className="mb-lg-4 mb-0">
                <strong>Finevideo, Inc.</strong>
                <br />
                . ,  .
                <br />
                . , . 0
                <br />
                <br />
                <strong>:</strong>
                <br />
                8-999-999-9999
                <br />
                8-888-888-8888
                <br />
                <strong>Email:</strong>
                <br />
                <S.ActionLink href="mailto:support@finevideo.ru">
                  support@finevideo.ru
                </S.ActionLink>
                <br />
                <S.ParagraphRF>   </S.ParagraphRF>
              </address>
            </div>
          </div>
        </div>
      </S.Footer>
      <Copyright />
    </>
  );
};
